:root {
  --clr-red: #ff7979;
  --clr-green: #38cc8b;
  --clr-blue: #5e53a4;
  --clr-blue-gray: #b9b6d3;
  --clr-dark-blue: #3e3c49;
  --clr-black: #000;
  --clr-white: #fff;
  --clr-border: #dedede;
  --ff-sans: "Poppins", Arial, Helvetica, sans-serif;
  --ff-mono: "Source Code Pro", monospace;
  --ff-serif: Cambria, Cochin, Georgia, serif;
  --fs-4xlarge: 3.815rem;
  --fs-3xlarge: 3.052rem;
  --fs-2xlarge: 2.44125rem;
  --fs-xlarge: 1.953125rem;
  --fs-large: 1.75rem;
  --fs-med: 1.25rem;
  --fs-reg: 1rem;
  --fs-small: 0.8rem;
  --fs-xsmall: 0.64rem;
  --gap: 1.25rem;
  --columns: 2;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

body {
  padding: 5.5rem 1.5rem 4.25rem;
  position: relative;
  background: url("../../images/bg-intro-mobile.png") center/cover no-repeat, var(--clr-red);
  font-family: var(--ff-sans);
  font-weight: 500;
  font-size: var(--fs-reg);
  line-height: 1.6;
}
@media (min-width: 64em) {
  body {
    background: url("../../images/bg-intro-desktop.png") center/cover no-repeat, var(--clr-red);
  }
}

html,
body {
  margin: 0px auto;
  width: 100%;
  box-sizing: border-box;
  color: var(--clr-white);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

/* prettier-ignore */
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.15;
  letter-spacing: -0.022em;
  margin: 0;
  margin-bottom: 1.25rem;
}

h1 {
  font-size: var(--fs-large);
}

p {
  font-size: var(--fs-reg);
  margin-bottom: 1.25rem;
}

.small,
small {
  font-size: var(--fs-small);
}

.xsmall {
  font-size: var(--fs-xsmall);
}

.disclaimer {
  margin-bottom: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 0.6875rem;
  line-height: 1.3125rem;
  letter-spacing: 1px;
  /* or 191% */
  text-align: center;
  color: var(--clr-blue-gray);
}
.disclaimer a {
  font-weight: 700;
  color: var(--clr-red);
  text-decoration: none;
}

