.btn--submit {
  cursor: pointer;
  display: block;
  width: 100%;
  padding: 0.9375em 2.6875em;
  margin-bottom: 0.5rem;
  border: none;
  background: #38cc8b;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.0908818);
  border-radius: 5px;
  color: #ffffff;
  font-style: normal;
  font-weight: 600;
  font-size: 0.9375rem;
  line-height: 1.625rem;
  /* identical to box height, or 173% */
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.cta {
  margin-bottom: 1.5rem;
  padding: 1.125em 4.125em;
  text-align: center;
  background: #5e54a4;
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.14688);
  border-radius: 10px;
  font-size: 0.9375rem;
  font-style: normal;
  line-height: 26px;
  font-weight: 400;
  /* or 173% */
  letter-spacing: 0.267857px;
}
.cta span {
  font-weight: 700;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

.form {
  margin-bottom: 1.5rem;
  padding: 1.5rem;
  width: 100%;
  background: #ffffff;
  color: var(--clr-dark-blue);
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.14688);
  border-radius: 10px;
}
.form__item:not(:last-child) {
  margin-bottom: 1rem;
}

input {
  border: 1px solid var(--clr-border);
  border-radius: 0.3125rem;
  height: 3.5rem;
  display: flex;
  align-items: center;
  padding: 0 1.25rem;
  width: 100%;
}
input::placeholder {
  color: var(--clr-dark-blue);
}

.hero {
  text-align: center;
  margin-bottom: 4rem;
}
.hero h1 {
  font-style: normal;
  font-weight: 700;
  font-size: 1.75rem;
  line-height: 2.25rem;
  /* or 129% */
  letter-spacing: -0.291667px;
}
.hero p {
  font-style: normal;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.625rem;
  /* or 162% */
}
@media (min-width: 64em) {
  .hero {
    text-align: left;
    margin-bottom: 0;
  }
  .hero h1 {
    font-size: 3.125rem;
    line-height: 3.4375rem;
    letter-spacing: -0.52px;
  }
}

.container {
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 64em) {
  .container {
    max-width: 69.375rem;
  }
}

.row {
  display: flex;
  flex-direction: column;
}
@media (min-width: 64em) {
  .row {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}

@media (min-width: 64em) {
  .col-half {
    width: 33.75rem;
  }
}